ight:40px;padding-bottom:30px}.positions{color:white;padding-bottom:20px}.projects-main{border-top:2px solid silver;margin:50px;margin-top:100px;min-height:800px;display:flex;flex-direction:column;align-items:center}@media (max-width: 767px){.projects-main{margin-top:0px}}.projects-main-title{margin-top:100px;margin-bottom:90px;font-family:'Pacifico';font-size:46px}@media (max-width: 767px){.projects-main-title{margin-top:60px}}.projects-icon-container{display:flex;justify-content:space-between;min-width:50vw;margin-bottom:90px}@media (max-width: 767px){.projects-icon-container{flex-wrap:wrap}}.icon{height:70px;width:70px}@media (max-width: 767px){.icon{padding:10px 10px}}.projects-container{display:flex;justify-content:center;font-size:13px;flex-wrap:wrap}.project-card{padding:18px;padding-top:5px;width:270px;height:350px;margin:8px;margin-top:20px;border-style:outset;border-radius:7px;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.projects-img{width:300px;height:130px;object-fit:cover}.github-link{width:80px}.App{text-align:start}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.contact-container{background-color:#171717;height:180px;color:white;display:flex;justify-content:space-evenly;align-items:center}.contact-container-text{font-size:14px;line-height:26px;display:flex;flex-direction:column}.linkedin-box{width:40px;height:40px;border-radius:5px;background-color:white}.linkedin-logo{object-fit:cover;width:40px;height